:root {
  /* Colors */
  --nyse-color-primary: #003d7a;      /* NYSE Blue */
  --nyse-color-secondary: #0055a5;
  --nyse-color-accent: #00a0e3;       /* Bright Accent */
  --nyse-color-dark: #1a1a1a;         /* Heading/Strong Text */
