}

/* 5. Form Elements */
input, textarea, select {
  font-family: var(--nyse-font-sans);
  font-size: 16px;
  padding: var(--nyse-spacing-sm) var(--nyse-spacing-md);
  border: 1px solid var(--nyse-color-border);
  border-radius: 4px;
  background-color: var(--nyse-color-background);
  color: var(--nyse-color-text);
  transition: border-color 0.3s ease, box-shadow 0.3s ease;
}

input:focus, textarea:focus, select:focus {
  outline: none;
  border-color: var(--nyse-color-primary);
  box-shadow: 0 0 0 3px rgba(0, 61, 122, 0.1);
}

/* 6. Buttons */
button {
  font-family: var(--nyse-font-sans);
  font-size: 16px;
  font-weight: 600;
  padding: var(--nyse-spacing-sm) var(--nyse-spacing-lg);
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
}

button:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

/* 7. Lists */
ul, ol {
  margin-top: 0;
  margin-bottom: var(--nyse-spacing-md);
  padding-left: var(--nyse-spacing-xl);
}

li {
  margin-bottom: var(--nyse-spacing-xs);
}

/* 8. Code */
code {
  font-family: var(--nyse-font-mono);
  background-color: var(--nyse-color-background-alt);
  padding: var(--nyse-spacing-xs) var(--nyse-spacing-sm);
  border-radius: 3px;
  font-size: 0.9em;
}

pre {
  background-color: var(--nyse-color-background-alt);
  padding: var(--nyse-spacing-md);
  border-radius: 4px;
  overflow-x: auto;
  margin-bottom: var(--nyse-spacing-md);
}

pre code {
  background: none;
  padding: 0;
}

/* 9. Images */
img {
  max-width: 100%;
  height: auto;
  display: block;
}

/* 10. Accessibility */
@media (prefers-reduced-motion: reduce) {
  *, *::before, *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
